@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
.slide-container{
    display: flex;
}

.slide-main{
    box-sizing: border-box;
    padding: 5px;
    width: 70vw;
    height: 60vh;
    background-color: blanchedalmond;
    border: 10px groove darkred;
    border-left: none;
    border-radius: 0 10px 10px 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.slide-text{
    overflow-y: scroll;
}

.next-button{
    justify-self: flex-end;
    align-self: flex-end;
}

.slide-aside{
    width: 5vw;
    height: 60vh;
    background-color: darkred;
    border-radius: 10px 0 0 10px;
    border-right: 1px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
}

.slide-aside > span{
    font-size: 15px;
    -webkit-writing-mode: vertical-rl;
        -ms-writing-mode: tb-rl;
            writing-mode: vertical-rl;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    letter-spacing: 15px;
    color: #DAA520;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.next-button{
    font-size: 40px;
    cursor: pointer;
    
}

.next-button:hover{
    color: darkred;
}

@media only screen and (min-width: 500px) and (max-width: 1919px){
    .slide-main{
        height: 90vh;
        width: 60vw;
    }
    .slide-aside{
        height: 90vh;

    }
    .slide-aside > span{
        font-size: 25px;
        -webkit-writing-mode: vertical-rl;
            -ms-writing-mode: tb-rl;
                writing-mode: vertical-rl;
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
        letter-spacing: 10px;
        color: #DAA520;
        -webkit-user-select: none;
            -ms-user-select: none;
                user-select: none;
    }
    .slide-text{
        padding: 0 5px;
        font-size: 30px;
        overflow-y: hidden;
    }
}

@media only screen and (min-width: 1920px) {
    .slide-main{
        height: 90vh;
        width: 60vw;
    }
    .slide-aside{
        height: 90vh;

    }
    .slide-aside > span{
        font-size: 30px;
        -webkit-writing-mode: vertical-rl;
            -ms-writing-mode: tb-rl;
                writing-mode: vertical-rl;
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
        letter-spacing: 20px;
        color: #DAA520;
        -webkit-user-select: none;
            -ms-user-select: none;
                user-select: none;
    }
    .slide-text{
        padding: 0 5px;
        font-size: 40px;
        overflow-y: hidden;
    }
}
.lastSlide-text{
    overflow-y: scroll;
}

@-webkit-keyframes heartbeat {
    0%{
        -webkit-transform: scale(.85);
                transform: scale(.85);
    }
    50%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100%{
        -webkit-transform: scale(.85);
                transform: scale(.85);
    }
}

@keyframes heartbeat {
    0%{
        -webkit-transform: scale(.85);
                transform: scale(.85);
    }
    50%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100%{
        -webkit-transform: scale(.85);
                transform: scale(.85);
    }
}

a{
    color: red;
}

.heart-container{
    display: flex;
    justify-content: center;
    color: red;
    font-size: 20vw;
    -webkit-animation: heartbeat 3000ms infinite;
            animation: heartbeat 3000ms infinite;
}
.heart{
    cursor: pointer;
}

.teamo{
    text-align: center;
    font-style: italic;
    letter-spacing: 6px;
    margin: 0;
}



@media only screen and (min-width: 500px) and (max-width: 1919px){
    .lastSlide-text{
        padding: 0 5px;
        font-size: 30px;
        overflow-y: scroll;
    }
}

@media only screen and (min-width: 1920px) {
    .lastSlide-text{
        padding: 0 5px;
        font-size: 40px;
        overflow-y: scroll;
    }
}

/* width */
::-webkit-scrollbar {
    width: .4vw;
}
::-webkit-scrollbar-track {
background-color: rgb(238, 219, 191);
}
::-webkit-scrollbar-thumb {
background-color: white;

}

::-webkit-scrollbar-thumb:hover {
background-color: white;
}
.start-container{
    box-sizing: border-box;
    width: 95vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgb(255,255,255);
    background: radial-gradient(circle, rgba(200,200,200,1) 0%, rgba(100,100,100,1) 100%);
    text-align: center;
    padding: 40px;
    border: 2px solid gray;
    border-radius: 10px;
    transition: all 1s;
}

.start__title{
    font-size: 50px;
}

.start__subtitle{
 font-size: 30px;
}

.start__button{
    font-family: 'Lobster', cursive;
    padding: 15px;
    font-size: 20px;
    background-color: rgb(167, 0, 0);
    color: white;
    border-radius: 10px;
    cursor: pointer;
}

@media only screen and (min-width: 1000px) {
    .start-container{
        width: 30vw;
    }
}

body{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Lobster', cursive;
}

.main{
    background: #00000099;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
