.lastSlide-text{
    overflow-y: scroll;
}

@keyframes heartbeat {
    0%{
        transform: scale(.85);
    }
    50%{
        transform: scale(1);
    }
    100%{
        transform: scale(.85);
    }
}

a{
    color: red;
}

.heart-container{
    display: flex;
    justify-content: center;
    color: red;
    font-size: 20vw;
    animation: heartbeat 3000ms infinite;
}
.heart{
    cursor: pointer;
}

.teamo{
    text-align: center;
    font-style: italic;
    letter-spacing: 6px;
    margin: 0;
}



@media only screen and (min-width: 500px) and (max-width: 1919px){
    .lastSlide-text{
        padding: 0 5px;
        font-size: 30px;
        overflow-y: scroll;
    }
}

@media only screen and (min-width: 1920px) {
    .lastSlide-text{
        padding: 0 5px;
        font-size: 40px;
        overflow-y: scroll;
    }
}

/* width */
::-webkit-scrollbar {
    width: .4vw;
}
::-webkit-scrollbar-track {
background-color: rgb(238, 219, 191);
}
::-webkit-scrollbar-thumb {
background-color: white;

}

::-webkit-scrollbar-thumb:hover {
background-color: white;
}