.slide-container{
    display: flex;
}

.slide-main{
    box-sizing: border-box;
    padding: 5px;
    width: 70vw;
    height: 60vh;
    background-color: blanchedalmond;
    border: 10px groove darkred;
    border-left: none;
    border-radius: 0 10px 10px 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.slide-text{
    overflow-y: scroll;
}

.next-button{
    justify-self: flex-end;
    align-self: flex-end;
}

.slide-aside{
    width: 5vw;
    height: 60vh;
    background-color: darkred;
    border-radius: 10px 0 0 10px;
    border-right: 1px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
}

.slide-aside > span{
    font-size: 15px;
    writing-mode: vertical-rl;
    transform: rotate(180deg);
    letter-spacing: 15px;
    color: #DAA520;
    user-select: none;
}

.next-button{
    font-size: 40px;
    cursor: pointer;
    
}

.next-button:hover{
    color: darkred;
}

@media only screen and (min-width: 500px) and (max-width: 1919px){
    .slide-main{
        height: 90vh;
        width: 60vw;
    }
    .slide-aside{
        height: 90vh;

    }
    .slide-aside > span{
        font-size: 25px;
        writing-mode: vertical-rl;
        transform: rotate(180deg);
        letter-spacing: 10px;
        color: #DAA520;
        user-select: none;
    }
    .slide-text{
        padding: 0 5px;
        font-size: 30px;
        overflow-y: hidden;
    }
}

@media only screen and (min-width: 1920px) {
    .slide-main{
        height: 90vh;
        width: 60vw;
    }
    .slide-aside{
        height: 90vh;

    }
    .slide-aside > span{
        font-size: 30px;
        writing-mode: vertical-rl;
        transform: rotate(180deg);
        letter-spacing: 20px;
        color: #DAA520;
        user-select: none;
    }
    .slide-text{
        padding: 0 5px;
        font-size: 40px;
        overflow-y: hidden;
    }
}