.start-container{
    box-sizing: border-box;
    width: 95vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgb(255,255,255);
    background: radial-gradient(circle, rgba(200,200,200,1) 0%, rgba(100,100,100,1) 100%);
    text-align: center;
    padding: 40px;
    border: 2px solid gray;
    border-radius: 10px;
    transition: all 1s;
}

.start__title{
    font-size: 50px;
}

.start__subtitle{
 font-size: 30px;
}

.start__button{
    font-family: 'Lobster', cursive;
    padding: 15px;
    font-size: 20px;
    background-color: rgb(167, 0, 0);
    color: white;
    border-radius: 10px;
    cursor: pointer;
}

@media only screen and (min-width: 1000px) {
    .start-container{
        width: 30vw;
    }
}